<template>
  <v-app>
    <div v-if="authenticated">
      Runaton username account: {{user.email}}
      <br />
      Runaton email account: {{user.email}}
    </div>
    <div id="display">
      <span id="timer">00:00:00.00</span>
    </div>
    <v-btn v-show="!started && meters===0" fab large v-on:click="trackPosition()">START</v-btn>
    <v-btn v-show="started" fab large v-on:click="stopTracking()">STOP</v-btn>
    <v-card v-show="!started && meters>0" elevation="2">
      <div id="map" style="width: 100% !important; height: 500px !important;"></div>
    </v-card>
    <v-btn v-if="authenticated && !started && meters>0" color="success">Salva corsa</v-btn>

    <v-btn v-show="!started && meters>0" v-on:click="meters=0" color="info">Resetta</v-btn>

    <div v-if="meters>0">{{realtime_meters}}</div>
  </v-app>
</template>
<script>
import mapboxgl from "mapbox-gl";
import { mapGetters } from "vuex";

const geo = {
  id: undefined,
};
import Vue from "vue";
Vue.use(geo);
Vue.use(mapboxgl);

var coordinates = [];
let lastTwoCoordinates = [];

export default {
  data: () => ({
    soglia: "",
    started: "",
    meters: "",
    realtime_meters: "",
  }),
  name: "Run",
  computed: {
    ...mapGetters({
      authenticated: "auth/authenticated",
      user: "auth/user",
    }),
  },
  created() {
    this.soglia = 0;
    this.started = false;
    this.drawMap();
    this.meters = 0;
    this.realtime_meters = "";
  },
  methods: {
    //***********************Geolocation*********************/
    trackPosition: async function () {
      this.started = !this.started;
      if (!navigator.geolocation) {
        alert("Localizzazione GPS non autorizzata o non supportata!");
        this.started = !this.started;
        return;
      }
      try {
        await navigator.wakeLock.request("screen");
      } catch (err) {
        console.log(`${err.name}, ${err.message}`);
        alert(
          "Questo browser non supporta questa funzionalità. Attualmente Chrome, Edge e Opera sono i browser supportati"
        );
        this.started = !this.started;
        return;
      }
      coordinates = [];
      geo.id = navigator.geolocation.watchPosition(
        this.successPosition,
        this.failurePosition,
        {
          enableHighAccuracy: true,
          timeout: Infinity,
          maximumAge: 5000,
        }
      );
    },
    successPosition: function (position) {
      if (
        (this.soglia += 1) > 5 &&
        position.coords.accuracy <
          (process.env.NODE_ENV === "production" ? 20 : Infinity)
      ) {
        coordinates.push([position.coords.longitude, position.coords.latitude]);
        lastTwoCoordinates.push([
          position.coords.longitude,
          position.coords.latitude,
        ]);
        if (lastTwoCoordinates.length > 1) {
          this.meters += this.realtimeMeters(lastTwoCoordinates);
          this.realtimeMetersPrint(this.meters);
          lastTwoCoordinates.shift();
          console.log(this.meters);
        }
        console.log(coordinates);
        console.log(position.coords.accuracy);
      }
    },
    failurePosition: function (err) {
      alert(
        "Codice di errore: " + err.code + "Messaggio di errore: " + err.message
      );
      navigator.geolocation.clearWatch(geo.id);
      coordinates = [];
      this.started = !this.started;
      return;
    },

    stopTracking() {
      this.started = !this.started;
      navigator.geolocation.clearWatch(geo.id);
      this.drawMap();
    },
    //********************Mapbox Draw Map******************/
    drawMap() {
      const length = coordinates.length;
      this.soglia = 0;

      if (length > 1) {
        mapboxgl.accessToken = process.env.VUE_APP_MAPBOX_API;
        var map = new mapboxgl.Map({
          container: "map",
          style: "mapbox://styles/mapbox/streets-v11",
          center: coordinates[Math.floor(coordinates.length / 2)],
          zoom: 15,
        });
        map.on("dataloading", () => {
          window.dispatchEvent(new Event("resize"));
        });

        map.on("load", function () {
          map.addSource("route", {
            type: "geojson",
            data: {
              type: "Feature",
              properties: {},
              geometry: {
                type: "LineString",
                coordinates: coordinates,
              },
            },
          });
          map.addLayer({
            id: "route",
            type: "line",
            source: "route",
            layout: {
              "line-join": "round",
              "line-cap": "round",
            },
            paint: {
              "line-color": "#f26958",
              "line-width": 8,
            },
          });
        });
      }
    },
    //**********************Realtime Meters*******************/
    realtimeMeters(lastTwoCoordinates) {
      if (
        lastTwoCoordinates[0][0] !== lastTwoCoordinates[1][0] &&
        lastTwoCoordinates[0][1] !== lastTwoCoordinates[1][1]
      ) {
        let puntoA = new mapboxgl.LngLat(
          lastTwoCoordinates[0][0],
          lastTwoCoordinates[0][1]
        );
        let puntoB = new mapboxgl.LngLat(
          lastTwoCoordinates[1][0],
          lastTwoCoordinates[1][1]
        );
        return puntoA.distanceTo(puntoB);
      }
      return 0;
    },
    realtimeMetersPrint(meters) {
      let realtime_meters = Math.floor(meters);
      if (realtime_meters >= 1000) {
        this.realtime_meters = `${(realtime_meters / 1000).toFixed(3)} km`;
      } else if (realtime_meters > 1) {
        this.realtime_meters = `${realtime_meters} metri`;
      } else if (realtime_meters === 1) {
        this.realtime_meters = `1 metro`;
      } else {
        this.realtime_meters = `0 metri`;
      }
    },
    //**********************Timer**************************/
  },
};
</script>

<style lang="css">
@import "https://api.mapbox.com/mapbox-gl-js/v2.2.0/mapbox-gl.css";
*:focus {
  outline: none;
}
.mapboxgl-ctrl-attrib-button {
  display: none !important;
}
.mapboxgl-ctrl-bottom-left,
.mapboxgl-ctrl-bottom-right {
  display: none !important;
}
</style>
